.back{
    position: absolute;
    background-color: #ffffff;
    width: 80%;
    height: auto;
}

.allUsers{
    position: absolute;
    top: 20px;
    left: 15px;
    width: 15%;
    height: 41px;
    background-color: #FFDE80;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
    border-radius: 3px;
}

.new_post_span{
    position: absolute;
    right: 25%;
    bottom: 28%;
    margin-left: 30px;
    color: rgba(255, 0, 0, 0);
}

.new_post_span_active{
    position: absolute;
    right: 25%;
    bottom: 28%;
    margin-left: 30px;
    color: rgba(255, 0, 0, 1);
}

.textArea{
    margin-top: 20px;
    margin-left: 50%;
    transform: translate(-50%, 0);
    display: flex;
    width: 57%;
    height: 41px;
    right: 0.3%;
    bottom: 10%;
    font-size: 30px;

}
.textArea input{
    position: relative;
    width: 88%;
    height: 35px;
}
.textArea button{
    position: relative;
    width: 140px;
    height: 41px;
    background-color: #FFDE80;
    font-size: 18px;
    font-weight: 600;
    border-radius: 3px;
    cursor: pointer;
}

.usersListItem{
    display: flex;
    border: #e3e5fc solid 1px;
}

.imgAndButton{
    margin: 30px;
}

.imgBlock{
    width: 100px;
    height: 100px;
    overflow: hidden;
    justify-content: center;
    align-items: center;
    display: flex;
    border-radius: 50%;
}

.imgBlock img{
    min-height: 100%;
    user-select: none;
}

.folButton button{
    margin-top: 10px;
    width: 100px;
    height: 35px;
    user-select: none;
    background-color: #FFDE80;
    border-radius: 3px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}

.allDescription{
    position: relative;
    border-left: rgb(202, 202, 202) solid 1px;
    width: 100%;
}

.name{
    margin: 20px;
    font-size: 120%;
    color: black;
}

.status{
    margin: 20px;
    position: absolute;
    bottom: 0;
}

.location span{
    display: table;
}

.location{
    position: absolute;
    right: 0;
    top: 0;
    margin: 15px;
    font-size: 120%;
}

.unselected{
    font-size: 120%;
    color: rgba(0, 0, 0, 0.6);
}

.selected{
    font-size: 120%;
    font-weight: 700;
    color: rgba(0, 0, 0, 1);
}

.pagesNumbers{
    display:inline-flex;
    margin: 15px;
    font-size: 130%;
    font-weight: 600;
    user-select: none;    
    max-width: 97%;
    overflow-x: scroll;
}

.pagesNumbers div{
    margin-right: 15px;
    cursor: pointer;
}

.usersNav{
    text-decoration: none;
}